.certif{
    padding: 10%;
    width: 100%; 
    display: inline-block;
}

.h_gallery_item {
    display: inline-block;
    margin-bottom: 45px; 
}
.h_gallery_item .g_img_item {
    position: relative;
    text-align: center;
    overflow: hidden;
    border-radius: 5px; 
}
.h_gallery_item .g_img_item:before {
    content: "";
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    background-image: linear-gradient(0deg, rgba(0,0,0,.3) 0%, rgba(0,0,0,.7) 100%) ; 
    position: absolute;
    opacity: 0;
    transition: all 300ms ease; 
}
  
.h_gallery_item:hover .g_img_item:before {
    opacity: .85; 
}
.h_gallery_item .g_img_item .light {
    position: absolute;
    left: 0px;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
    opacity: 0; 
}
.h_gallery_item:hover .g_img_item .light {
    opacity: 1;
}
  
.h_gallery_item .g_item_text {
    text-align: center; 
}
.h_gallery_item .g_item_text h4 {
    color: #000e29;
    font-size: 16px;
    margin-top: 55px;
    margin-bottom: 24px;
    transition: all 300ms linear 0s; 
}

@media (max-width:480px){
    .h_gallery_item .g_item_text h4{
      font-size: 12px;
    }
}

.center .slick-center div {
    opacity: 1;
    -ms-transform: scale(1.08);
    transform: scale(1.08);
}
  
.center div{
    transition: all .3s ease;
}
    
.slick-next::before,.slick-prev:before{
    color: #000e29;
}
  
.center img{
    display: inline;
}