.SideDrawer {
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top:0;
    z-index: 200;
    background-color: #000e29;
    padding: 32px 16px;
    box-sizing: border-box;
    transition:transform 0.3s ease-out;
}
@media (min-width:991px){
    .SideDrawer{
        display: none;
    }
}
span{
    font-size: 25px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    color: #fff;
    transition: all 300ms linear 0s;
  }
  
.Open{
transform: translateX(0);
}

.Close{
transform: translateX(-100%);
}


.Nav3{
    background-color: transparent;
    line-height: 0;
    height: 0;
    margin-top: 40px;
}
.Nav3 ul{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
    height: 100%;
    flex-flow: column;
}
.Nav3 ul li{
    margin-bottom: 30px;
    box-sizing: border-box;
    display: block;
    width: 100%;
    align-items: center;
    cursor: pointer;
    text-transform: uppercase;
    border-bottom: 1px solid #ededed33
}

.Nav3 ul li a{
    font: 600 14px/30px "Roboto", sans-serif;
    color: white;
}