.homeBannerArea {
  margin-bottom: 200px;
}
.Container {
  border-radius: 12px;
  position: relative;
  bottom: -150px;
  background: #fff;
  padding: 30px 15px 30px 15px;
  box-shadow: 0px 20px 80px 0px rgba(153, 153, 153, 0.3);
}
.bannerInner {
  width: 100%;
  min-height: 600px;
}
.bannerContent {
  text-align: left; 
}

.img{
  width: 65%;
  margin: auto;
  border-radius: 12px;
  box-shadow:1px 20px 80px 0px rgba(153, 153, 153, .5) ;
}

.personalText h6 {
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2.1px;
  font-weight: normal;
  margin-bottom: 12px;
  color: #000e29; 
}

.personalText h4 {
  font-size: 16px;
  font-weight: 500;
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  margin-bottom: 70px;
  color: #000e29; 
}

.personalText h3 {
  font-size: 48px;
  text-transform: uppercase;
  margin-bottom: 15px;
  color: #000e29; 
}

.personalText p {
  font-family: "Roboto", sans-serif;
  max-width: 540px;
  color: #777777;
  margin-bottom: 40px; 
}

.basicInfo li {
  margin-bottom: 15px; 
}

.basicInfo li span {
  position: relative;
  padding-left: 40px;
  font-size: 16px;
  color:#777777; 
}

.basicInfo li span i {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  color: #000e29  ; 
}

.basicInfo li:last-child {
  margin-bottom: 0px; 
}

.moreBtn {
  text-align: center;
  margin-top: 80px;
  margin-right: 0; 
}

.mainBtn {
  background-color: #000e29;
  display: inline-block;
  padding: 0px 30px;
  color: #fff !important ;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  font-weight: 500;
  line-height: 48px;
  outline: none !important;
  text-align: center;
  cursor: pointer;
  border-radius: 5px;
  text-transform: uppercase; 
  border: 1px solid #000e29;
 }

.mainBtn:hover {
  background: transparent;
  color: #000e29 !important; 
}

@media (max-width:1199px){
	.homeBannerArea {
		min-height: 728px;
		margin-bottom: 120px;
		padding-bottom: 120px;
	}
	.homeBannerArea .bannerInner {
		min-height: 650px;
	}

	.bannerContent .Media{
		display: block;
	}
	.homeBannerArea .bannerContent{
		max-width: 500px;
		margin: auto;
	}
	.homeBannerArea .bannerInner .bannerContent .Media .dFlex {
		padding-right: 0px;
		display: block;
		width: 100%;
	}
	.homeBannerArea .bannerInner .bannerContent .Media .dFlex img {
		width: 100%;
		height: 100%;
	}
	.homeBannerArea .bannerInner .bannerContent .Media .mediaBody {
		margin-top: 30px;
	}
	.homeBannerArea .Container {
		bottom: -130px;
	}
	.personalText {
		padding-left: 0px;
	}
	.homeBannerArea .banner_inner{
		min-height: 728px;
	}
	.homeBannerArea {
		margin-bottom: 0px;
	}
}


@media (max-width:991px){
	.homeBannerArea{
		padding-bottom: 0px	;
	}
	.homeBannerArea .Container {
    bottom: -60px;
	}
	.personalText {
		padding-top: 30px;
	}
}

@media (max-width:767px){
	.homeBannerArea {
		min-height: 570px;
		margin-bottom: 0px;
	}
	.homeBannerArea .bannerInner {
    	min-height: 500px;
	}
	.homeBannerArea .Container {
		min-height: 500px;
	}
	.homeBannerArea .bannerInner .bannerContent {
		margin-top: 0px;
	}

	.homeBannerArea .bannerInner .bannerContent h3 {
		font-size: 45px;
		line-height: 55px;
	}
	.homeBannerArea .bannerInner .bannerContent p br {
		display: none;
	}
	.homeBannerArea .bannerInner .bannerContent h3 span {
		line-height: 45px;
		padding-bottom: 0px;
		padding-top: 0px;
 	}
  
}


@media (max-width:575px){
	.homeBannerArea .bannerInner .bannerContent h2 {
		font-size: 28px;
	}
	.homeBannerArea {
		min-height: 570px;
	}
	.homeBannerArea .bannerInner {
		min-height: 500px;
	}
	.homeBannerArea .bannerInner .bannerContent h5 {
		margin-top: 0px;
	}
	.homeBannerArea .Container {
		bottom: -56px;
		border-radius: 0px;
	}
	.homeBannerArea {
		margin-bottom: 0px;
	}	
}
@media (max-width:480px){
	.bannerInner .bannerContent h2 {
		font-size: 32px;
	}
}
  
