.mainTitle {
    position: relative;
    text-align: center;
    max-width: 670px;
    margin: 0px auto 75px; 
}
.mainTitle h2 {
    font-family: "Heebo", sans-serif;
    font-size: 50px;
    color: #2c3e50;
    margin-bottom: 15px;
}  
.mainTitle::after{
    position: absolute;
    content: "";
    width: 122px;
    height: 0.18rem;
    left: 0;
    right: 0;
    margin: auto;
    top: 60px;
    background-color: #000e29;
}

@media (max-width:575px){
    .container{
      padding-right: 26px;
        padding-left: 26px;
    }
    .mainTitle h2{
      font-size:36px;
    }
    
    .mainTitle::after{
      top: 45px;
    }
}
  
  