.Nav{
    height: 100%;
}

.navItem {
    height: 100%;
    padding:0 20px; 
}

.navLink {
    font: 600 14px/120px "Roboto", sans-serif;
    cursor: pointer;
    text-transform: uppercase;
    color: #fff;
    padding: 0px;
    line-height: 66px; 
    font-size: 40px;
}

@media (max-width:991px){
    .navLink {
        font-size: 40px;
    }     
}

@media (max-width:575px){
    .navLink {
      font-size: 35px;
    }    
}



