
.homeGalleryArea{
  padding-top: 120px;
  padding-bottom: 60px;
}

.isotopeFillter {
  margin-bottom: 60px; 
}

.faExternalLink:before {
  left: 2px;
  position: relative;
  top: 2px;
}

.Card {
  margin-bottom: 60px;
}

.Card .cardContent .cardTitle {
  line-height: 2.5rem;
}

.Card .cardContent .tealText {
  color: #000e29 !important;
}

.Card ul {
  margin-bottom: 0;
  margin-top: 25px;
}
.Card ul li {
  line-height: 200%;
  list-style-type: disc;
  margin-left: 24px;
  color: #222;
}

.Card .cardTitle {
  font-size: 24px;
  color: #000e29;
  display: inline !important;
}

.Card .cardImage img{
  height: 100%;
}

.cardImage {
  height: 240px;
}

.cardContent {
  padding-bottom: 30px;
}

.cardContent p {
  padding: 5px 0 0;
  color: #222;
}

.cardContent i{
  color: #000e29;
}


span i.Right {
  margin-left: 10px;
}

.cardReveal span i{
  color:#000e29
}


.cardAction i{
  background-color:#000e29
}

.cardAction a.btnFloating {
  margin-bottom: 0;
  margin-right: 15px;
}


@media (max-width:767px){
  .Card .cardTitle {
    font-size: 17px;
  }
  .container{
    max-width: 90%;
  }
}

@media (max-width:575px){
  .Card .cardTitle {
    font-size: 24px;
  }
}










          