
.contactArea{
  padding-top: 120px;
  padding-bottom: 120px;
}  
  
.contactForm .formGroup {
  margin-bottom: 10px; 
}
    

.submitBtn {
  margin-top: 20px;
  background-color: #000e29;
  display: inline-block;
  padding: 0px 30px;
  color: #fff !important ;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  font-weight: 500;
  line-height: 48px;
  outline: none !important;
  text-align: center;
  cursor: pointer;
  border-radius: 5px;
  text-transform: uppercase; 
  border: 1px solid #000e29; 
}
.submitBtn:hover {
  background: transparent;
  color: #000e29 !important; 
}

.submitBtn:disabled,
.submitBtn[disabled]{
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.2; 
}


      

