}
.socialWidget{
  text-align: center;
}

.socialWidget .fTitle {
  margin-bottom: 35px; 
}
.socialWidget .fTitle h3 {
  color: #fff;
  font-size: 30px;
  font-weight: bold;
  font-family: "Heebo", sans-serif;
  margin-bottom: 0px; 
}
.socialWidget p {
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", sans-serif;
  color: #777777;
  margin-bottom: 20px; 
}
.socialWidget p:last-child{
  margin-bottom: 0px;
}
.socialWidget .List{
  margin-bottom: 20px;
}
.socialWidget .List li {
  margin-right: 20px;
  display: inline-block; 
}
.socialWidget .List li a {
  color: #cccccc;
  font-size: 25px;
  transition: all 300ms linear 0s; 
  cursor: pointer;
}
.socialWidget .List li:last-child {
  margin-right: 0px; 
}
.socialWidget .List li:hover a {
  color: #777777; 
} 



