.myTabsArea {
  padding-top: 120px;
  padding-bottom: 100px;   
}
.Nav{
  display: block;
  text-align: center;
  border: none;
  margin-bottom: 70px; 
}
.Nav li {
  display: inline-block;
  margin-right: 8px; 
}
.Nav li span {
  background-color: #000e29;
  display: inline-block;
  padding: 0px 30px;
  color: #fff !important ;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  font-weight: 500;
  line-height: 48px;
  outline: none !important;
  text-align: center;
  cursor: pointer;
  border-radius: 5px;
  text-transform: uppercase; 
  border: 1px solid #000e29;
}

.resumeTitle h3{
  text-align: center;
  margin-bottom:30px;
  color: #000e29;
  font-size: 33px;
  margin-bottom:48px
}

.Nav li span.active{
  background: transparent;
  color: #000e29 !important; 
}

@media (max-width:991px){
  .myTabsArea{
    padding-top: 120px;
  }
}

@media (max-width:575px){
  .Nav li span{
    padding: 0 25px;
  }
  .resumeTitle h3{
    font-size: 20px;
  }
}


@media (max-width:480px){
  .resumeTitle h3{
    margin-bottom:0;
   
  }
  .resumeTitle h3{
    margin-bottom:30px
  }
}

