.List {
    max-width: 100%;
    margin: auto;
    position: relative;
    padding-top: 40px;
    padding-bottom: 120px; 
}
.List:before {
    content: "";
    height: 100%;
    width: 5px;
    background:rgba(0, 14, 41, 0.5);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -5px; 
}
.List li {
    margin-bottom: 15%;
    position: relative; 
}
.List li span {
    height: 15px;
    width: 15px;
    border-radius: 50%;
    display: block;
    background: rgba(0, 14, 41, 0.5);
    position: absolute;
    left: 50%;
    top: 30px;
    transform: translateX(-50%); 
}
.List li span:before {
    content: '';
    height: 7px;
    width: 7px;
    background: #000e29;
    border-radius: 50%;
    position: absolute;
    left: 52%;
    top: 4px;
    transform: translateX(-50%); 
}
.List li:last-child {
    margin-bottom: 0px; 
}

.dFlex p {
    color: #222;
    margin-bottom: 0px;
    padding-top: 20px; 
}
.dFlex {
    /* padding-left: 33%;  */
    position: absolute;
    left: 33%;
}

.mediaBbody{
    position: absolute;
    left: 59%;
    /* padding-left: 18%; */
  }
.mediaBbody h4 {
    color: #222;
    font-size: 21px;
    text-transform: uppercase;
    margin-bottom: 6px; 
}
.mediaBbody p {
    color: #222;
    margin-bottom: 0px; 
}

@media (max-width:1199px){
    .dFlex{
        position: absolute;
        left: 30%;
    }
    .mediaBbody{
        position: absolute;
        left: 61%;
    }
    .List li {
        margin-bottom: 18%;
    }
    .List {
        padding-bottom: 150px;
    }
}
