.galleryFilter {
    text-align: center; 
}
.galleryFilter li {
    display: inline-block;
    margin-right: 45px; 
    cursor: pointer;
}
.galleryFilter li:last-child {
    margin-right: 0px; 
}
.galleryFilter li span {
    font-size: 20px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    color: #222222;
    transition: all 300ms linear 0s;
    text-transform: uppercase;
}
.galleryFilter li:hover span,.galleryFilter li.active span {
    color: #fff;
    background-color: #000e29;
    padding: 5px 10px; 
}


@media (max-width:480px){
    .galleryFilter li span {
        font-size: 13px;
    }
}

